import LoginUtils from '../static/js/login.js'

import Notify from '../wxcomponents/vant/dist/notify/notify.js'
import Buttonuu from '../wxcomponents/vant/dist/button'
import Image from '../wxcomponents/vant/dist/image'
import Overlay from '../wxcomponents/vant/dist/overlay/index.js'
import CellGroup from '../wxcomponents/vant/dist/cell-group/index.js'

export default {
	
	props: {
		loginUser: {
			type: Object,
			required: true
		}
	},
	components: {
		'van-button': Buttonuu,
		'van-image': Image,
		'van-overlay': Overlay,
		'van-cell-group': CellGroup
	},
	data() {
		return {
			nickname: this.loginUser.nickname || '',
			schoolName: this.loginUser.schoolName || '',
			stuNumber: this.loginUser.stuNumber || '',
		}
	},
	methods: {
		cancel() {
			this.$emit('closeUpdateUserInfoModal');
		},
		submit() {
			let nickname = this.nickname;
			let schoolName = this.schoolName;
			let stuNumber = this.stuNumber;
			// console.log(nickname, schoolName, stuNumber);
			
			if (null == nickname || null == schoolName || null == stuNumber) {
				Notify({ type: 'danger', message: "内容不能为空" });
				return;
			}
			
			nickname = nickname.trim();
			schoolName = schoolName.trim();
			stuNumber = stuNumber.trim();
			
			if ("" === nickname) {
				Notify({ type: 'danger', message: "用户名不能为空" });
				return;
			}
			if ("" === schoolName) {
				Notify({ type: 'danger', message: "学校名不能为空" });
				return;
			}
			let p = new RegExp("[0-9]+");
			if (!p.test(stuNumber)) {
				Notify({ type: 'danger', message: "学生号不能为空且需要是数字" });
				return;
			}
				
			LoginUtils.updateUserInfo({
				nickname,
				schoolName,
				stuNumber
			});
			this.$emit('closeUpdateUserInfoModal');
		}
	}
}
